<template>
  <v-app>
    <v-container>
      <v-row>
        <v-col cols="12">
          <h1>Jonathan Law</h1>
        </v-col>
      </v-row>

      <v-row class="sticky-header" style="top: 0">
        <v-col cols="12" class="hr-line pa-0"></v-col>
      </v-row>

      <Header :sheet="sheet" v-on:toggle_sheet="sheet = !sheet"
      :page_title="page_title" :navigation_routes="navigation_routes"></Header>

      <NavigationMobile v-if="this.$vuetify.breakpoint.mobile" :sheet="sheet"
                        v-on:toggle_sheet="sheet = !sheet" :navigation_routes="navigation_routes"></NavigationMobile>

      <v-row class="sticky-header" style="top: 65px;">
        <v-col cols="4" class="hr-line mt-0 mb-4 pt-0 pb-0"></v-col>
      </v-row>

<!--      <transition :enter-active-class="'animated ' + transition_direction">-->
      <transition>
        <keep-alive>
          <router-view></router-view>
        </keep-alive>
      </transition>

      <Footer :last_update="last_update"></Footer>
    </v-container>
  </v-app>
</template>

<script>
  export default {
    name: 'App',
    components: {
      Header: () => import('./components/Header'),
      NavigationMobile: () => import('./components/NavigationMobile'),
      Footer: () => import('./components/Footer')
    },
    data: () => ({
      sheet: false,
      page_title: '',
      pages: {'/' : 0, '/background' : 1, '/works' : 2},
      transition_direction: "slideInRight",
      navigation_routes: [
        { title: "HOME", route: "/"},
        { title: "MY BACKGROUND", route: "/background"},
        { title: "WORKS", route: "/works"},
      ],
      last_update: ""
    }),
    created() {
      fetch('https://api.github.com/repos/jonathanlawhh/jonathanlawhh.github.io/commits/gh-pages')
              .then(res => res.json())
              .then(r => {
                if (r.commit !== undefined) {
                  let d = new Date(r.commit.author.date).toDateString().split(" ");
                  this.last_update = `${d[1]} ${d[3]}`;
                } else {
                  this.last_update = "|| Github API rate limit hit :("
                }
              });
    },
    mounted() {
      let spa_param = location.search.substr(1).split("=");
      (spa_param[0] === "spa") && this.$router.push(spa_param[1]);

      window.fbAsyncInit = function() {
        // eslint-disable-next-line no-undef
        FB.init({ xfbml: true, version: 'v8.0' });
      };

      (function(d, s, id) {
        var js, fjs = d.getElementsByTagName(s)[0];
        if (d.getElementById(id)) return;
        js = d.createElement(s); js.id = id;
        js.src = 'https://connect.facebook.net/en_US/sdk/xfbml.customerchat.js';
        fjs.parentNode.insertBefore(js, fjs);
      }(document, 'script', 'facebook-jssdk'));
    },
    watch: {
      $route: {
        immediate: true,
        handler(to, from) {
          this.transition_direction = (from && this.pages[from.path] > this.pages[to.path]) ? "slideInLeft" : "slideInRight"
          this.page_title = to.meta.title || 'Lost';
        }
      },
    },
    metaInfo () {
      return {
        title: this.$route.meta.title,
        titleTemplate: '%s'
      }
    }
  };
</script>

<style>
  .sticky-header {
    position: sticky;
    z-index: 10;
  }

  .hr-line {
    border-bottom: aliceblue 1px solid;
    width: 100%;
  }

  .btn_close:hover {
    transform: rotate(180deg);
  }

  .v-btn--active::before {
    opacity: 0 !important;
  }

  .v-btn--active {
    border-bottom: white solid 1px;
    border-radius: 0 !important;
  }

  .v-btn::before{
    background-color: transparent;
  }
</style>

<style lang="scss">
  @import url('https://fonts.googleapis.com/css2?family=Rubik:wght@300&display=swap');
  @import url('https://fonts.googleapis.com/css?family=Material+Icons&display=swap');
  $animationDuration: 1s !default;
  $quoteAnimationNames: false !default;
  @import '~vue2-animate/src/sass/make-transitions';
  @import '~vue2-animate/src/sass/animations/sliding/all';

  #app {
    font-family: 'Rubik', sans-serif;
  }
</style>
