import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

  const routes = [
  {
    path: '/',
    name: 'Home',
    component: () => import('../views/Home.vue'),
    meta: { title: "Hello" }
  },{
    path: '/background',
    name: 'Background',
    component: () => import('../views/Background.vue'),
    meta: { title: "Background" }
  },{
    path: '/works',
    name: 'Works',
    component: () => import('../views/Works.vue'),
      meta: { title: "Works" }
  },{
    path: '/works/ai',
    name: 'WorksAIML',
    component: () => import('../views/WorksAIML'),
    meta: { title: "Works / AI, ML" }
  },{
    path: '/works/graphics',
    name: 'WorksGraphics',
    component: () => import('../views/WorksGraphics.vue'),
    meta: { title: "Works / Graphic" }
  },{
    path: '/works/websites',
    name: 'WorksWebsites',
    component: () => import('../views/WorksWebsites.vue'),
    meta: { title: "Works / Websites" }
  },{
    path: '*',
    name: 'Lost',
    component: () => import('../views/Lost.vue'),
    meta: { title: "Lost" }
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
